<template>
  <div>
    <el-tabs type="border-card">
      <el-tab-pane class="box" label="国内疫况">
        <el-tag class="medium gntotal" color="#FDF1F1">累计确诊人数:  {{this.list.gntotal}}</el-tag>
        <el-tag class="medium deathtotal" color="#F3F6F8">累计死亡人数:  {{this.list.deathtotal}}</el-tag>
        <el-tag class="medium sustotal" color="#FFF5E9">现有疑似人数:  {{this.list.sustotal}}</el-tag><br>
        <el-tag class="medium curetotal" color="#F1F8F4">累计治愈人数:  {{this.list.curetotal}}</el-tag>
        <el-tag class="medium econNum" color="#FDF1F1">现有确诊人数:  {{this.list.econNum}}</el-tag>
        <el-tag class="medium heconNum" color="#FAF2F6">现有重症人数:  {{this.list.heconNum}}</el-tag>
      </el-tab-pane>
      <el-tab-pane class="box" label="国外疫况">
        <el-tag color="#FDF1F1" class="medium gntotal">累计确诊人数:  {{this.othertotal.certain}} <span>较上日{{this.othertotal.certain_inc}}</span></el-tag>
        <el-tag class="medium deathtotal" color="#F3F6F8">累计死亡人数:  {{this.othertotal.die}} <span>较上日{{this.othertotal.die_inc}}</span></el-tag><br>
         <el-tag class="medium sustotal" color="#FFF5E9">现有疑似人数:  {{this.othertotal.uncertain}} <span>较上日{{this.othertotal.uncertain_inc}}</span></el-tag>
         <el-tag class="medium curetotal" color="#F1F8F4">累计治愈人数:  {{this.othertotal.recure}} <span>较上日{{this.othertotal.recure_inc}}</span></el-tag>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
import jsonp from 'jsonp'
export default {
  name:'Data',
  data() {
    return {
      list:{},
      othertotal:{}
     };
   },
  mounted(){
     this.getData()
   },
  methods: {
	getData() {
		// jsonp 跨域请求
		jsonp(
			'http://interface.sina.cn/news/wap/fymap2020_data.d.json?_=1580892522427', {}, (erros, data) => {
				// console.log(data);
				// console.log(data.data.list)
        this.list = data.data
        this.othertotal = data.data.othertotal
        console.log(this.othertotal);
        
			})
    }
   }
}
</script>

