      font-size: 25px;
  }
  .el-tag--medium{
      position: relative;
      margin: 50px 0 0 15px;
      height: 100px;
      line-height: 100px;
      font-size: 25px;
  }
  .gntotal{
    color: #CC1E1E;
  }
  span{
    position: absolute;
    top: -30px;
